<template>
  <div class="container-md" style="margin-top: 64px;">
    <form class="row justify-content-center" @submit.prevent="signup">
      <div class="col-md-6">
        <div class="alert alert-danger" role="alert" v-if="errorMsg">
          {{ errorMsg }}
        </div>
        <h1 class="h3 mb-3 font-weight-normal">會員註冊</h1>
        <div class="mb-2">
          <label for="inputUsername" class="sr-only">姓名：</label>
          <input
            type="text"
            id="inputUsername"
            class="form-control"
            placeholder="Email address"
            required
            autofocus
            v-model="user.username"
          />
        </div>
        <div class="mb-2">
          <label for="inputEmail" class="sr-only">Email address</label>
          <input
            type="email"
            id="inputEmail"
            class="form-control"
            placeholder="Email address"
            required
            autofocus
            v-model="user.email"
          />
        </div>
        <div class="mb-2">
          <label for="inputPassword" class="sr-only">Password</label>
          <input
            type="password"
            id="inputPassword"
            class="form-control"
            placeholder="Password"
            required
            v-model="user.password"
          />
        </div>
        <div class="mb-2">
          <label for="role">身份</label>
          <select id="role" class="form-select mb-3" v-model="user.role">
            <option selected disabled >請選擇身份</option>
            <option value="student">學生</option>
            <option value="instructor">講師</option>
          </select>
        </div>
        <div class="text-end mt-4">
          <button class="btn btn-lg btn-primary btn-block" type="submit">註冊</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      user: {
        username: '',
        email: '',
        password: '',
        role: ''
      },
      errorMsg: ''
    }
  },
  methods: {
    signup () {
      const api = `${process.env.VUE_APP_API}user/register`
      this.$http.post(api, this.user)
        .then((res) => {
          console.log(res)
          window.alert('註冊成功，您現在將被導向登入頁面')
          this.$router.push('/login')
        })
        .catch((err) => {
          this.errorMsg = err.response.data
        })
    }
  }
}
</script>
